import IsMobile from "../module/IsMobile";

function ChromeScheme() {
	const schme = "scheme=http;package=com.android.chrome;end";
	const redirect = "intent://sangheonyi.github.io/myNote/chrome_scheme#Intent;";

	if (IsMobile())
		window.location.href= redirect + schme;
	else
		window.location.href= "sangheonyi.github.io/myNote/";
	return <h1>Loading...</h1>;
}

export default ChromeScheme;
